import React from 'react'

class SearchInput extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      entry: ''
    }
  }
  onSubmitForm = (event)=>{
    event.preventDefault();
    this.props.onSearchForm(this.state.entry);

  }

  render() {
    return (
      <div className="row justify-content-center m-4">
        <div className="" style={{border:'1px solid #00000026'}}>
          <form onSubmit={this.onSubmitForm} className="form px-3 py-2 m-2">
            <input type="text" placeholder="Search" 
            onChange={(event)=>{this.setState({entry: event.target.value});console.log(event.target.value)} }
            value={this.state.event} 
            />
          </form>
        </div>
      </div>
    )
  }
}

export default SearchInput;