import React from 'react'
import SearchInput from './SearchInput'
import axios from 'axios'
import ImageList from './ImageList'



class App extends React.Component {
  constructor(props){
    super(props)
    this.state={image: []}
    this.onSearchForm = this.onSearchForm.bind(this)
  }
  async onSearchForm(entry){
    const response = await axios.get(`https://pixabay.com/api/?key=22670330-c18db668f1bacb36cf0640bf0&q=${entry}&image_type=photo`)
    this.setState({image: response.data.hits });
  }

render(){
  return(
    <div>
    <SearchInput onSearchForm={this.onSearchForm}/>
    <ImageList images = {this.state.image} />
    </div>
  )
}
}

export default App;